/*======> React Modules <======*/
import * as React from "react";

/*=======> Define Controls Props <=======*/
interface ControlProps {
    id?,
    className?,
    type?,
    size?,
    name?,
    placeholder?,
    value?,
    //===> Events <===//
    onInput? : React.EventHandler<any>,
    onChange?: React.EventHandler<any>,
    onFocus? : React.EventHandler<any>,
    onClick? : React.EventHandler<any>,
    onBlur?  : React.EventHandler<any>,
}

/*======> Form Control Component <======*/
export default class FormControl extends React.Component<ControlProps> {
    /*====> Constructor Method <====*/
    constructor(props) {
        super(props);
    }
    /*======> Render Function <======*/
    render() {
        /*======> Assign Properties <======*/
        const {
            type,
            id,
            className,
            size,
            name,
            placeholder,
            ...attrs
        } = this.props;
        //====> Form Control Handler <====//
        const formControler = () => {
            //====> Get Props <====//
            var props = this.props;
            //====> Select Handler <====//
            if(props.type == 'select') {
                //===> Select Control <===//
                return null;
            } else {
                //===> Standard Input <===//
                return (<input id={id} name={name} type={type} className={`form-control ${className?className:''}`} placeholder={placeholder} {...attrs} />);
            }
        }
        /*====> Render Content <====*/
        return (formControler());
    }
}
